<template>
    <section class="calender-container">

        <div class="calender">

            <div class="calender_header">
                <p> {{coin}} </p>
                <p @click="pick"> ></p>
            </div>

            <div class="calender_main">
                <div class="calender_main__item"
                     :style="{color:existDate.includes(item.formDate) ? '#fff' :''}"
                     v-for="(item,index) in visibeDays"
                     :class="{pro_month_active: item.date.getMonth()+1 !== date.getMonth()+1}"
                     :key="index"
                     :date-formdate="item.formDate">
                    {{item.date.getDate()}}

                    <div class="active" v-if="existDate.includes(item.formDate)"></div>
                </div>
                <div class="calender_main__nowMonth">{{nowMonth}}</div>
            </div>

            <button class="calender_button" @click="sign"></button>

        </div>

    </section>
</template>

<script>
    export default {
        name: "calender",
        props: {
            date: {
                type: Date,
                default: () => {
                    return new Date()
                }
            },
            existDate: {
                type: Array,
                default: []
            },
            coin: {
                type: Number,
                default: 0
            },
        },
        methods: {
            sign() {
                this.$emit('sign')
            },
            pick(){
                this.$emit('pick')
            }
        },
        computed: {
            /**
             * 42
             * */
            visibeDays() {

                //  /
                let {year, month} = this.$Utils.getYearMonthDay(this.date);

                // 
                let currentFirstDay = this.$Utils.getDate(year, month, 1);

                // 
                let week = currentFirstDay.getDay();

                // 
                let start = currentFirstDay - week * 60 * 60 * 1000 * 24;

                let arr = [];

                for (let i = 0; i < 42; i++) {

                    let y = new Date(start + i * 60 * 60 * 1000 * 24).getFullYear();
                    let m = parseInt(new Date(start + i * 60 * 60 * 1000 * 24).getMonth() + 1) < 10 ? "0" + parseInt(new Date(start + i * 60 * 60 * 1000 * 24).getMonth() + 1) : parseInt(new Date(start + i * 60 * 60 * 1000 * 24).getMonth() + 1);
                    let d = new Date(start + i * 60 * 60 * 1000 * 24).getDate() < 10 ? "0" + new Date(start + i * 60 * 60 * 1000 * 24).getDate() : new Date(start + i * 60 * 60 * 1000 * 24).getDate();

                    arr.push({
                        date: new Date(start + i * 60 * 60 * 1000 * 24),
                        formDate: `${y}-${m}-${d}`
                    })
                }

                return arr;

            },
            nowMonth() {
                let {month} = this.$Utils.getYearMonthDay(this.date);
                return month + 1
            }
        }
    }
</script>

